<template>
	<div class="exam-main" :style="{minHeight: wrapHeight + 'px'}">
		<div class="eaxm-header" ref="header" v-show="!loading">
			<div class="count-down">倒计时：<span>{{timer}}</span></div>
			<div class="btn-block">
				<van-button plain type="primary" size="small" @click="onClick" :disabled="status!==1">交卷</van-button>
			</div>
		</div>
		<div class="exam-content">
			<div v-if="nodata" class="no-data"></div>
			<template v-else>
				<div class="eaxm-content-main" v-show="!loading">
					<ul>
						<template v-for="(item, index) in questions">
							<li :key="index" :id="'q'+index">
								<div class="question-type">{{types[item.subjectType*1]}}</div>
								<div class="question-content">
									<div class="question-title">{{index+1}}. {{item.title}}</div>
									<van-radio-group class="question-options" v-model="item.answer" v-if="item.subjectType*1===0||item.subjectType*1===2">
										<template v-for="(option, i) in item.op">
											<van-radio :name="i+1" :disabled="status!==1">{{option}}</van-radio>
										</template>
									</van-radio-group>
									<van-checkbox-group class="question-options" v-model="item.answer" v-if="item.subjectType*1===1">
										<template v-for="(option, i) in item.op">
											<van-checkbox shape="square" :name="i+1" :disabled="status!==1">{{option}}</van-checkbox>
										</template>
									</van-checkbox-group>
								</div>
							</li>
						</template>
					</ul>
					<Answer v-show="!loading" id="sheet" :disabled="status!==1" :current="activeIndex" @change="onChange" :data="questions"></Answer>
				</div>
			</template>
		</div>
		<div class="exam-footer">
			<div class="answer-sheet-btn">
				<van-button type="primary" @click="showAnswerSheet" block :disabled="!total||status!==1">{{currentIndex}}/{{total}}</van-button>
			</div>
			<div class="btns-group">
				<van-button type="primary" @click="onPrev" block :disabled="!activeIndex||status!==1">上一题</van-button>
				<van-button type="primary" @click="onNext" block :disabled="activeIndex+1===total||!total||status!==1">下一题</van-button>
			</div>
		</div>
	</div>
</template>

<script>
	import { mapState } from "vuex"
	import Answer from './answer'
	const types = ['单选题', '多选题', '判断题']
	export default {
		name: 'exam',
		data() {
			return {
				contentH: 0,
				gobackTimer: null,
				loading: true, // 加载中
				nodata: false, // 无数据
				types: types, // 题目类型
				limit: null, // 考试时限
				count: null,
				timer: '00:00:00',
				status: null, // 考试状态（1：考试中）
				interval: null, // 计时器
				intervalTask: null, // 轮询器
				activeIndex: 0, // 当前题目
				total: 0, // 题目总数
				result: {}, //考试信息
				questions: [] // 题目
			}
		},
		watch: {
			questions: {
				handler(newVal, oldVal) {
					if(this.questions.length && this.status === 1) {
						this.cacheData()
					}
				},
				deep: true
			}
		},
		computed: {
			...mapState(['info']),
			wrapHeight() {
				return this.contentH
			},
			currentIndex() {
				return this.total === 0 ? this.activeIndex : this.activeIndex + 1
			}
		},
		components: {
			Answer
		},
		mounted() {
			this.$nextTick(() => {
				this.contentH = document.documentElement.clientHeight
			})
			this.getData()
		},
		methods: {
			// 缓存答题情况
			cacheData() {
				let studentid = this.info.id
				let traniid = this.$route.query.id
				let examid = this.$route.query.examid
				let id = studentid + '_' + traniid + '_' + examid
				this.$Utils.setItem(id, this.questions, 10 / 24 / 60) //缓存10分钟
			},
			// 获取缓存答题情况
			getCacheData() {
				let studentid = this.info.id
				let traniid = this.$route.query.id
				let examid = this.$route.query.examid
				let id = studentid + '_' + traniid + '_' + examid
				let questions = this.$Utils.getItem(id)
				return questions
			},
			//轮询请求是否回收试卷
			pollingTask() {
				this.intervalTask = setInterval(() => {
					this.getExamStatus()
				}, 3000)
			},
			// 获取考试状态
			async getExamStatus() {
				let params = {
					studentId: this.info.id,
					examinationId: this.$route.query.examid
				}
				let res = await this.$Request.get('/examination/queryEcCodeByWeChat.do', params)
				if(res && res.code === 0) {
					if(res.examinationCode === 2) { //后台回收试卷
						this.sureSubmit(2) // 后台回收试卷，自定提交
					}
				} else {
					this.$notify.clear()
				}
			},
			// 时间格式化
			formatDate(date) {
				date = this.$Utils.toDate(date)
				return this.$Utils.dateFtt('yyyy-MM-dd hh:mm:ss', date)
			},
			//获取考试时间
			getExamTime(time, ctime) {
				let examCloseTime = this.$Utils.toDate(time).getTime() // 考试关闭时间
				// let currTime = new Date().getTime() // 当前时间 
				let currTime = this.$Utils.toDate(ctime).getTime() // 服务器当前时间 
				let leftTime = examCloseTime - currTime
				this.status = leftTime > 0 ? 1 : 0
				this.count = parseInt(leftTime / 1000)
				if(this.count > 0) {
					this.timer = this.getFmtTime(this.count)
					this.initTimer()
					this.pollingTask()
				}
			},
			// 获取试卷内容
			async getData() {
				let params = {
					studentId: this.info.id,
					examinationId: this.$route.query.examid
				}
				let res = await this.$Request.get('/examination/getTestPaperByWeChat.do', params)
				this.loading = false
				if(res && res.code === 0) {
					this.formatQuestion(res)
				} else {
					this.nodata = true
				}
			},
			// 格式化问卷数据
			formatQuestion(res) {
				let obj = res.list || {}
				let questions = this.getCacheData()
				if(questions && questions.length && !this.$route.query.isreexam) {
					questions = questions
				} else {
					let temp = obj.content ? JSON.parse(obj.content) : []
					temp.forEach(item => {
						item.answer = item.subjectType * 1 === 1 ? [] : null
					})
					if(obj.isDisorder) { //题目乱序
						let temp1 = temp.filter(item => item.subjectType * 1 === 0) // 单选题
						temp1 = this.shuffle(temp1)
						let temp2 = temp.filter(item => item.subjectType * 1 === 1) // 多选题
						temp2 = this.shuffle(temp2)
						let temp3 = temp.filter(item => item.subjectType * 1 === 2) // 判断题
						temp3 = this.shuffle(temp3)
						let arr = [...temp1, ...temp2, ...temp3]
						questions = arr
					} else {
						questions = temp
					}
				}
				this.updateExamStatus(obj, questions)
			},
			//修改成考试中的状态
			async updateExamStatus(result, questions) {
				let params = {
					studentId: this.info.id,
					examinationId: this.$route.query.examid
				}
				let res = await this.$Request.get('/examination/updateECbyExamination.do', params)
				if(res && res.code === 0) {
					console.log(res.list, questions)
					this.result = result
					this.questions = questions
					this.total = questions.length
					this.nodata = !questions.length
					this.getExamTime(res.list, res.nowTime)
				}
			},
			// 开始计时
			initTimer() {
				this.interval = setInterval(() => {
					if(this.count === 0) {
						this.clearInterval()
						this.sureSubmit(1) //到达考试时限，自动提交试卷
					} else {
						this.count--
							this.timer = this.getFmtTime(this.count)
					}
				}, 1000)
			},
			// 获取计时时间
			getFmtTime(num) {
				let h = parseInt(num / (60 * 60) % 60)
				let min = parseInt((num / 60) % 60)
				let s = parseInt(num % 60)
				h = h < 10 ? '0' + h : h
				min = min < 10 ? '0' + min : min
				s = s < 10 ? '0' + s : s
				return h + ':' + min + ':' + s
			},
			// 停止计时
			clearInterval() {
				clearInterval(this.interval)
				this.interval = null
			},
			// 停止轮询
			clearPollingTask() {
				clearInterval(this.intervalTask)
				this.intervalTask = null
			},
			// 数据洗牌
			shuffle(arr) {
				let cloneArr = arr.concat()
				let len = cloneArr.length
				for(let i = 0; i < len; i++) {
					let index = Math.floor(Math.random() * len)
					let temp = cloneArr[index]
					cloneArr[index] = cloneArr[i]
					cloneArr[i] = temp
				}
				return cloneArr
			},
			//上一题
			onPrev() {
				this.activeIndex--
					let top = document.getElementById('q' + this.activeIndex).offsetTop
				let h = this.$refs.header.offsetHeight
				window.scrollTo(0, top - h)
			},
			// 下一题
			onNext() {
				this.activeIndex++
					let top = document.getElementById('q' + this.activeIndex).offsetTop
				let h = this.$refs.header.offsetHeight
				window.scrollTo(0, top - h)
			},
			// 跳转答题卡
			showAnswerSheet() {
				let top = document.getElementById('sheet').offsetTop
				let h = this.$refs.header.offsetHeight
				window.scrollTo(0, top - h)
			},
			// 跳转指定题目
			onChange(index) {
				this.activeIndex = index
				let top = document.getElementById('q' + this.activeIndex).offsetTop
				let h = this.$refs.header.offsetHeight
				window.scrollTo(0, top - h)
			},
			// 点击交卷
			onClick() {
				this.$dialog.confirm({
					title: '是否确定交卷？',
					className: 'custom-confirm-dialog'
				}).then(() => {
					// on confirm
					this.sureSubmit()
				}).catch(() => {
					// on cancel
				})
			},
			//确定提交
			async sureSubmit(type) {
				let temp = JSON.parse(JSON.stringify(this.questions))
				temp.forEach(item => {
					if(item.subjectType * 1 !== 1) {
						item.answer = [item.answer]
					}
				})
				//console.log(temp)
				let para = {
					studentId: this.info.id,
					examinationId: this.$route.query.examid,
					jsonData: JSON.stringify(temp)
				}
				//console.log(para)
				let res = await this.$Request.post('/examination/saveTpByWeChat.do', para)
				this.clearPollingTask()
				if(res && res.code === 0) {
					if(type) {
						if(type === 2) {
							this.$toast.success('老师回收试卷，您的试卷已自动提交！')
						} else {
							this.$toast.success('考试时限已到，您的试卷已自动提交！')
						}
					} else {
						this.$toast.success('提交成功！')
					}
					this.gobackTimer = setTimeout(() => {
						if(history.length > 2) {
							history.back()
						} else {
							this.$router.replace({
								path: '/examlist',
								query: {
									id: this.$route.query.id
								}
							})
						}
					}, 1500)
				}
			}
		},
		beforeDestroy() {
			clearTimeout(this.gobackTimer)
			this.clearInterval()
			this.clearPollingTask()
		}
	}
</script>

<style>

</style>


