<template>
	<div class="answer-main">
		<div class="answer-main-title">答题卡</div>
		<div class="answer-main-subtitle">本卷总共{{data.length}}道题，总分{{totalScore}}分</div>
		<div class="answer-legend">
			<span class="active"><a></a><span>已答</span></span>
			<span><a></a><span>未答</span></span>
		</div>
		<div>
			<div></div>
			<div class="answer-list">
				<template v-for="(item, i) in data">
					{{item.answer}}
					<a v-if="item.subjectType*1===1" :class="{answered:item.answer.length,current:current===i}" @click="onClick(i)">{{i+1}}</a>
					<a v-else :class="{answered:item.answer,current:current===i}" @click="onClick(i)">{{i+1}}</a>
				</template>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'answer',
		props: {
			data: {
				type: Array,
				default: () => {
					return []
				}
			},
			disabled: {
				type: Boolean,
				default: false
			},
			current: {
				type: Number
			}
		},
		computed: {
			totalScore() {
				let total = 0
				this.data.forEach(item => {
					total += item.score*1
				})
				return total
			}
		},
		data() {
			return {
			}
		},
		mounted() {
			console.log(this.data)
		},
		methods: {
			onClick(i) {
				if(!this.disabled){
					this.$emit('change', i)
				}
			}
		}
	}
</script>

<style>

</style>


